
.App{
  scroll-snap-type: y mandatory;
  overflow-y: scroll;
  height: 100vh;
}

.App::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.App {
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}


.grid-container{
  height: 100%;
  border-bottom: 1px solid black;
}

.section{
  height: 100%;
}

.image{
  height: 100px;
  width: 100px;
}

.lottie{
  width: 600px;
  height: 600px;
  z-index: 1;

}

.title{

  font-size: 48px !important;
  font-weight: 100 !important;
  color: #64BED7 !important;
  text-align: center !important;
}

#person{
 padding: 100px;
}


.foot{
  height: 100%;
}

.ext {
  color: wheat;
}

#start-text{
  width: 200px;
  font-size: 22px;
}
#end-text{
  text-align: right;
  margin-right: 10px;
  font-size: 22px;
}

.image {
  display: flex;
  flex-direction: column;
  cursor: pointer;
}

.image p{
  font-size: 16px;
  margin-left: 10px;
}

.feed p{
  color: #0B1D22;
  font-size: 22px;
  text-align: center;
  padding: 50px;
}

.feed {
  height: 80%;
  display: flex !important;
  flex-direction: column !important;
  justify-content: space-between !important;

}

@media screen and (max-width: 2700px) {
  #person{
    width: 400px !important;
  }
}
@media screen and (max-width: 1000px) {
  #person{
    width: 350px !important;
  }
}
@media screen and (max-width: 700px) {
  #person{
    width: 300px !important;
  }
}
@media screen and (max-width: 600px) {
  #person{
    width: 200px !important;
    padding: 50px;
  }
}


@media screen and (max-width: 600px) {

  .lottie{
    width: 300px;
    height: 200px;
  
  }
  #temp{
    text-align: center !important;
    font-size: 16px !important;
  }
  .image img{
    display: none;
  }
  #start-text{
    width: 100%;
    font-size: 18px;
  }
  
  #end-text{
    width: 100%;
    font-size: 18px;
    margin: 0px;
  }
 
}